import router from "./router";
import NProgress from "nprogress"; // progress bar
import "nprogress/nprogress.css"; // progress bar style
import { getToken } from "@/utils/auth"; // get token from cookie
import getPageTitle from "@/utils/get-page-title";
import store from "@/store";

NProgress.configure({ showSpinner: false }); // NProgress Configuration

const whiteList = ["/login", "/auth-redirect", "/history", "/", "/statistics"]; // 

router.beforeEach(async (to, from, next) => {
  // start progress bar
  NProgress.start();
  console.log(store.getters["login/newToken"]);
  console.log(Boolean(store.getters["login/newToken"]));
  console.log(store.getters["login/role"]);
  // set page title
  // document.title = getPageTitle(to.meta.title)
  // if()
  console.log(store.state.login.language);
  if (store.state.login.language == "en") {
    document.title = "Multi Vital Monitor";
  } else {
    document.title = "";
  }

  // determine whether the user has logged in
  const hasToken = getToken();
  // const userInfo = getUserInfo()

  if (hasToken) {
    //
    if (to.path === "/login") {
      // if is logged in, redirect to the home page
      next({ path: "/" });
      NProgress.done();
    } else {
      if (store.getters["login/role"] == 2) {
        //
        if (whiteList.indexOf(to.path) !== -1) {
          /*,*/
          next();
        } else {
          // 
          next({ path: "/" });
        }
      } else {
        //
        next();
      }
    }
  } else {
    //
    /* has no token*/
    // next(`/login?redirect=${to.path}`)
    if (to.path === "/login") {
      next();
    } else {
      next(`/login?redirect=${to.path}`);
    }
    NProgress.done();
  }
});

router.afterEach(() => {
  // finish progress bar
  NProgress.done();
});



